@import url('https://fonts.googleapis.com/css?family=Ubuntu&display=swap');

body {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Ubuntu', sans-serif;
   font-size: 20px;
   background: #482ff7;
}

.App {
   max-width: 90%;
   margin: 20px auto;
   color: #eee;
}

.navbar {
   max-width: 100%;
   padding: 10px 20px;
   text-align: center;
   background: #3827b3;
   border-radius: 20px;
}

.navbar h1 {
   margin: 10px 0;
}

.book-list {
   margin: 10px;
}
.book-list ul {
   padding: 0;
   list-style-type: none;
}
.book-list li {
   background: #b31e6f;
   border-radius: 4px;
   padding: 10px;
   cursor: pointer;
   margin: 10px 0;
   text-align: center;
}
.book-list li:hover {
   opacity: 0.7;
   text-decoration: line-through;
}
.book-list .title {
   font-weight: bold;
   color: #fff;
   font-size: 1.2em;
}
.book-list .author {
   font-size: 0.9em;
   color: #ddd;
}
.empty {
   margin: 20px;
   text-align: center;
}

form .input__text {
   /* margin: 10px; */
   display: flex;
   justify-content: space-between;
   align-items: center;
}

form .input__submit {
   display: flex;
   justify-content: space-around;
   align-items: center;
}

input[type='text'] {
   width: 90%;
   min-height: 40px;
   padding: 8px 5px;
   margin: 8px 10px;
   border: 2px ridge #b31e6f;
   border-radius: 10px;
   font-size: 1.2rem;
   font-weight: bold;
   background-color: #3827b3;
   color: #fff;
}

input[type='text']:focus {
   outline: none;
}

::placeholder {
   color: #ccc;
}

input[type='submit'] {
   width: 98%;
   height: 40px;
   padding: 20px 0;
   margin: 12px 0;
   background-color: transparent;
   border: 4px ridge #b31e6f;
   color: #fff;
   font-size: 1.2rem;
   font-weight: bold;
   display: flex;
   justify-content: center;
   line-height: 1px;
   transition: 0.5 all;
}
input[type='submit']:hover {
   outline: 3px groove #b31e6f;
}
